import { FC, useEffect, useState } from "react";
import { EV_PLAN_PROP } from "@/entities/EventPlan/type/event-plan-type";
import ALabel from "../Label/ALabel";
import * as style from '../Input.module.scss'
import { EV_CONTACT_PROP } from "@/entities/EventContact/type/event-contact-type";

// / Регулярные выражения для проверки email и телефона
const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
const phoneRegex = /^\+?[1-9]\d{1,14}$/;

interface AInputProps {
    label: string | null,
    errorMessage: string | null,
    nameForHandler: EV_PLAN_PROP | EV_CONTACT_PROP,
    handleChange: (
        type: EV_PLAN_PROP | EV_CONTACT_PROP, value: string
    ) => void,
    handleOnFocus: (code: EV_PLAN_PROP | EV_CONTACT_PROP, error: string) => void

}


const AInput: FC<AInputProps> = ({
    label,

    errorMessage,
    nameForHandler,
    handleChange,
    handleOnFocus,

}) => {
    const id = `input-${nameForHandler}`;
    const [inputValue, setInputValue] = useState<string>('');
    const [localError, setLocalError] = useState<string | null>(errorMessage);

    let type = 'text'
    if (nameForHandler === EV_CONTACT_PROP.EMAIL) {
        type = 'email'
    } else if (nameForHandler === EV_CONTACT_PROP.PHONE) {
        type = 'tel'
    }

    const validateInput = (value: string): boolean => {
        let isError = false
        if (nameForHandler === EV_CONTACT_PROP.EMAIL) {
            if (!emailRegex.test(value)) {
                isError = true
                setLocalError('Некорректный email');
            } else {
                setLocalError(null);
            }
        } else if (nameForHandler === EV_CONTACT_PROP.PHONE) {
            if (!phoneRegex.test(value)) {
                isError = true
                setLocalError('Некорректный телефон');
            } else {
                setLocalError(null);
            }
        }
        return isError;
    };

    useEffect(() => {
        setLocalError(errorMessage)
    }, [
        errorMessage
    ])
    return (
        <>
            <div className={`${style.inputWrapper} col-md-12`}>
                {label && <ALabel
                    htmlId={id}
                    label={label}
                    errorMessage={localError}

                />}
                <input
                    onChange={
                        (e) => {
                            if (localError) {
                                setLocalError('')
                            }
                            handleChange(
                                nameForHandler,
                                e.target.value
                            )

                        }}

                    onFocus={() => handleOnFocus(nameForHandler, localError)}
                    className={`${style.input} form-control ${localError ? 'is-invalid' : ''}`}
                    onBlur={(e) => validateInput(e.target.value)}
                    type={type}
                    // className={`form-control is-invalid`}
                    // defaultValue="2019-08-19"
                    id={"example-string-input" + type}
                    lang="rus"
                />
                {/* {localError && <div className="invalid-feedback">{localError}</div>} */}

            </div>

        </>
    );
}

export default AInput;