import React, { FC, ReactNode } from 'react';
import { Container, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
import * as classes from './Modal.module.scss'
import { AButton } from '../Button/AAButton';
import { Preloader, PreloaderCard } from '@/shared/Preloader';
import { ContactEventType, eventContactActions } from '@/entities/EventContact/model/EventContactSlice';
import { useAppDispatch } from '@/app/lib/hooks/redux';

interface ModalProps {
    color: 'white' | 'grey'
    type: ContactEventType
    isActive?: boolean
    children?: ReactNode  // Тип ReactNode охватывает все возможные типы, которые могут быть переданы как children
    cancel:() => void
}


const AModal: FC<ModalProps> = ({ color, type, isActive, cancel, children }) => {

    let pageClass = classes.pageWhite

    if (color === 'grey') {
        pageClass = classes.pageGrey
    }
   


    return (
        <React.Fragment>
            <Container fluid={true}
                className={`${pageClass} ${classes.page} p-0`}
            >
                <Modal
                    isOpen={isActive}
                    toggle={() => cancel()}
                    // className={className}
                    backdrop={true}
                    keyboard={true}
                    backdropClassName={classes.back}
                    size='small'
                >
                    {/* <ModalHeader toggle={() => alert('toggle')}>Modal title</ModalHeader>
                    <ModalBody> */}
                    {children}
                    {/* <div className={classes.preloader}> */}
                    {/* <PreloaderCard

                    /> */}

                    {/* </div> */}

                    {/* </ModalBody>
                    <ModalFooter> */}

                    {/* </ModalFooter> */}

                </Modal>



            </Container>
        </React.Fragment>
    );
}

export default AModal;